import React from 'react';
import Link from 'gatsby-link';
import { connect } from 'react-redux';
import classNames from 'classNames';
import Img from 'gatsby-image';

import logo from '../../img/logo.png';
import styles from '../../styles/navigation.module.css';

const NavigationLink = props =>
  <li>
    <Link to={props.to} onClick={props.toggleNav}>
      {props.children}
    </Link>
  </li>  

const Navigation = ({ showNav, toggleNav, links, backgroundImage }) =>
  <nav
    className={classNames({
      [`${styles.active}`]: showNav,
    })}
  >
    <Img 
      style={{
        zIndex: -1,
        position: "fixed",
        right: 0,
        bottom: 0,
        width: "100%",
        height: "100%",  
      }}
      sizes={backgroundImage.sizes}
    />
    <ul>
    {
      links.map(link => (
        <NavigationLink key={link.to} to={link.to} toggleNav={toggleNav}>
          {link.text}
        </NavigationLink>
      ))
    }
    </ul>
    <img className={styles.logo} src={logo} alt="Ingredienza Logo" />
  </nav>

const mapStateToProps = ({ showNav }) => {
  return { showNav }
}

const mapDispatchToProps = dispatch => {
  return { toggleNav: () => dispatch({ type: 'TOGGLE_NAV' }) }
}

const ConnectedNavigation = connect(mapStateToProps, mapDispatchToProps)(Navigation)
export default ConnectedNavigation;



